*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  font-family: var(--main-font);
  color: var(--main-txt-cl);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

h1, h2, h3, h4, h5, h6, p {
  letter-spacing: 0;
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

button {
  cursor: pointer;
}

address {
  font-style: normal;
}

:root {
  --mobile: 320px;
  --tablet: 768px;
  --desktop: 1360px;
  --main-font: "Open Sans", sans-serif;
  --main-txt-cl: #fff;
  --secondary-txt-cl: #333;
  --hero-txt-cl: #e9e9e9;
  --btn-txt-cl: #fff;
  --btn-blog-txt-cl: #0284d0;
  --background-dark-cl: #2f303a;
  --background-btn-white-cl: #fff;
  --background-header-cl: rgba(14, 15, 20, .6);
  --background-blog-cl: #0284d0;
  --background-contact-cl: #f4f4f4;
  --background-footer-cl: #333;
  --layout-team-cl: rgba(0, 0, 0, .6);
  --icon-social-cl: #fff;
  --border-btn: #fff;
  --placeholder-contact-cl: gray;
  --accent-cl: #28a745;
  --accent-hovered-cl: #279e42;
  --duration: 250ms;
  --delay: 250ms;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

